.news_container {
  margin: 32px;
  max-width: 1000px;
  min-width: 200px;
}

.story {
  background-color: #f5eae0;
  margin-bottom: 64px;
  width: 100%;
}

.news_title {
  font-weight: 600 !important;
  font-size: 32px !important;
}

.news_date {
  font-size: 24px !important;
  font-weight: 600 !important;
}

.news_text {
  padding: 32px !important;
  color: rgb(55, 41, 40) !important;
  font-weight: 600 !important;
  font-size: 24px !important;
}

.news_image {
  width: calc(100% - 40px);
  height: 300px;
}

.news_image > img {
  object-fit: cover;
  object-position: 50% 90%;
  width: 100%;
  height: 100%;
}

.news_diamond {
  height: 40px;
  width: 40px;
}

.news_diamond > img {
  width: 100%;
  height: 100%;
}
